<template>
    <div class="top">
        <h1>人员管理系统 V3.6</h1>
        <div style="float:right">
            <el-dropdown>
                <span class="el-dropdown-link">
                    {{manager.name}}
                    <i class="el-icon-arrow-down el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item v-if="manager.state===0"><a href="http://localhost:8080/dl">登录</a></el-dropdown-item>
                    <el-dropdown-item v-if="manager.state===1"><a href="http://localhost:8080/tc">退出</a></el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </div>
    </div>
</template>

<script>
import router from "../router";
import axios from "axios";
export default {
    data() {
        return {
            manager:{}
        };
    },
    created: function() {
        this.getState();
    },
    methods: {
        getState(){
            let here = this;
            axios({
                url: "/loginstate"
            })
                .then(function(response) {
                    here.manager = response.data;
                })
                .catch(function(error) {
                    console.log(error);
                });
        }
    }
};
</script>

<style lang="stylus" scoped>
.top {
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
    background: #222;
    height: 50px;
    color: aliceblue;
}
a{ 
text-decoration:none; 
color:#333; 
}
h1 {
    float: left;
    margin-bottom: 0;
    margin-top: 0px;
    margin-right: 20px;
    margin-left: 0px;
}

.el-dropdown-link {
    cursor: pointer;
    color: #409EFF;
}

.el-icon-arrow-down {
    font-size: 18px;
    margin-top: 8px;
}
</style>



