<template>
    <div>
        <el-menu
            default-active="1-1"
            @open="handleOpen"
            @close="handleClose"
            @select="handleSelect"
            background-color="#eeeeee"
            text-color="#000000"
            :collapse="isCollapse"
        >
            <el-submenu index="1">
                <template slot="title">
                    <i class="el-icon-user-solid"></i>
                    <span>人员管理</span>
                </template>
                <el-menu-item index="1-1">人员列表</el-menu-item>
                <el-menu-item index="1-2">薪酬管理</el-menu-item>
                <el-menu-item index="1-3">合同管理</el-menu-item>
            </el-submenu>
            <el-menu-item index="2">
                <i class="el-icon-info"></i>
                <span slot="title">日志管理</span>
            </el-menu-item>
            <el-submenu index="3">
                <template slot="title">
                    <i class="el-icon-document"></i>
                    <span>权限管理</span>
                </template>
                <el-menu-item index="3-1">管理员列表</el-menu-item>
                <el-menu-item index="3-2">角色管理</el-menu-item>
            </el-submenu>
        </el-menu>
    </div>
</template>

<script>
import router from '../router';
export default {
    data() {
        return {
            isCollapse: false
        };
    },
    methods: {
        handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        handleSelect(key, keyPath) {
            if(key=='1-1'){
                router.push('/');
            }else if(key=='1-2'){
                router.push('/xclist');
            }else if(key=='1-3'){
                router.push('/htlist');
            }else if(key=='2'){
                router.push('/log');
            }else if(key=='3-1'){
                router.push('/power');
            }else if(key=='3-2') {
                router.push('/role');
            }
        }
    }
};
</script>


