import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const list = r => require.ensure([], () => r(require('@/pages/list')), 'list')
const xclist = r => require.ensure([], () => r(require('@/pages/xclist')), 'xclist')
const htlist = r => require.ensure([], () => r(require('@/pages/htlist')), 'htlist')
const log = r => require.ensure([], () => r(require('@/pages/log')), 'log')
const power = r => require.ensure([], () => r(require('@/pages/power')), 'power')
const role = r => require.ensure([], () => r(require('@/pages/role')), 'role')
const tabs = r => require.ensure([], () => r(require('@/pages/tabs')), 'tabs')
const powertabs = r => require.ensure([], () => r(require('@/pages/powertabs')), 'powertabs')
const error = r => require.ensure([], () => r(require('@/pages/error')), 'error')
const routes = [
  {
    path: '/',
    component: list
  },
  {
    path: '/xclist',
    component: xclist
  },
  {
    path: '/htlist',
    component: htlist
  },
  {
    path: '/log',
    component: log
  },
  {
    path: '/error',
    component: error
  },
  {
    path: '/power',
    component: power
  },
  {
    path: '/role',
    component: role
  },
  {
    path: '/tabs',
    name: 'tabs',
    component: tabs
  },
  {
    path: '/powertabs',
    name: 'powertabs',
    component: powertabs
  }
]

export default new Router({
  routes,
  strict: process.env.NODE_ENV !== 'production'
})



